<template>
	<view class="content">
		<!-- 轮播图 -->
		<view class="swiper-wrapper">
			<loading :list="lunBoList" />
			<swiper class="swiper" :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000" circular="true">
				<swiper-item class="swiper-item" v-for="item in lunBoList" :key="item.id">
					<a :href="item.href">
						<image :src="item.img" mode="widthFix"></image>
					</a>
				</swiper-item>
			</swiper>
		</view>
		<!-- 菜单导航 -->
		<view class="menu-wrapper">
			<view class="nav">
				<view class="item">
					<view class="iconfont icon-ziyuan"></view>
					<text>咕噜超市</text>
				</view>
				<view class="item">
					<view class="iconfont icon-tupian"></view>
					<text>联系我们</text>
				</view>
				<view class="item">
					<view class="iconfont icon-guanyuwomen"></view>
					<text>社区图片</text>
				</view>
				<view class="item">
					<view class="iconfont icon-shipin"></view>
					<text>视频专区</text>
				</view>
			</view>
		</view>
		<!-- 推荐商品列表 -->
		<view class="goods-wrapper">
			<view class="hot_goods">
				<view class="tit">推荐商品</view>
				<!-- 一般用法 -->
				<view class="goods_list">
					<view class="goods_item" v-for="(item, index) in goodsList" :key="index">
						<image :src="item.img" style="width: 150px;"></image>
						<view class="price">
							<text>￥{{item.discountPrice}}</text>
							<text>￥{{item.price}}</text>
						</view>
						<view class="name">{{item.name}}</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import loading from '../../components/common/loading.vue'
	export default {
		data() {
			return {
				title: 'Hello',
				lunBoList: [],
				goodsList: [],
			}
		},
		components: {
			loading,
		},
		onLoad() {

		},
		methods: {

		},
		async mounted() {
			const {data: {lunBoList}} = await this.$api.getLunbo()
			const {data: {goodsList}} = await this.$api.getGoods()
			this.lunBoList = lunBoList
			this.goodsList = goodsList
		}
	}
</script>

<style lang="scss">
	// 轮播
	.swiper-wrapper {
		height: 150px;
		background-color: #333333;

		.swiper {
			.swiper-item {
				text-align: center;
			}
		}
	}

	// 菜单
	.menu-wrapper {
		.nav {
			display: flex;
			align-items: center;

			.item {
				width: 25%;
				text-align: center;

				view {
					background: $shop-color;
					line-height: 120rpx;
					width: 120rpx;
					height: 120rpx;
					border-radius: 90px;
					margin: 10px auto;
				}

				text {
					font-size: 15px;
				}
			}

			.iconfont {
				font-size: 25px;
				color: #fff;
				height: 50px;
			}

			.icon-tupian {
				font-size: 20px;
			}
		}
	}

	// 推荐商品列表
	.goods-wrapper {
		.hot_goods {
			background: #eee;

			.tit {
				border-top: 2px solid #eee;
				border-bottom: 2px solid #eee;
				margin-top: 20px;
				margin-bottom: 3px;
				color: $shop-color;
				height: 50px;
				line-height: 50px;
				text-align: center;
				letter-spacing: 20px;
				background: #fff;
			}

			.goods_list {
				display: flex;
				padding: 0 15rpx;
				justify-content: space-between;
				overflow: hidden;
				flex-wrap: wrap;

				.goods_item {
					width: 355rpx;
					margin-bottom: 15rpx;
					background: #fff;
					padding: 10px;
					box-sizing: border-box;

					image {
						height: 150px;
						width: auto;
						mix-width: 160px;
						margin: 10px auto;
					}

					.price {
						font-size: 18px;
						color: red;
						padding: 8px 0;

						text:nth-child(2) {
							color: #ccc;
							text-decoration: line-through;
							margin-left: 10px;
							font-size: 13px;
						}
					}

					.name {
						font-size: 14px;

					}
				}
			}
		}
	}
</style>
